<template>
    <div
        class="header-notification__menu-list__item"
        @click.stop="redirectTo(item.orderid, 'messages')"
    >
        <!--                          <v-btn class="header-notification__menu-list__item-close" @click.stop="deleteNotification(item.id)" text icon color="blue-grey lighten-3">-->
        <!--                            <i>close</i>-->
        <!--                          </v-btn>-->
        <div class="header-notification__menu-list__item-header">
            <div
                v-if="item.author"
                class="header-notification__menu-list__item-header__img header-notification__menu-list__item-header__img-orange"
            >
                <img
                    v-if="item.author.avatar.length > 0"
                    class="header-notification__menu-list__item-header__img-main"
                    :src="`/images/avatars/` + item.author.avatar"
                >
                <img
                    v-else
                    class="header-notification__menu-list__item-header__img-main"
                    src="/images/default-avatar.png"
                    alt=""
                >

                <div class="header-notification__menu-list__item-header__img-preview">
                    <img
                        src="/images/msg-icon.png"
                        alt=""
                    >
                </div>
            </div>
            <div class="header-notification__menu-list__item-header__content">
                <div class="header-notification__menu-list__item-header__content-header">
                    <span class="header-notification__menu-list__item-header__content-header__title">New message</span>
                    <span
                        v-if="item.author"
                        class="header-notification__menu-list__item-header__content-header__from"
                    >
                        <span v-if="item.author.nickname">
                            from {{ item.author.nickname }} ({{ item.author.sw_id }})
                        </span>
                        <span v-else>
                            from {{ item.author.firstname }} {{ item.author.lastname.charAt(0) }}. ({{ item.author.sw_id }})
                        </span>
                    </span>
                </div>

                <div class="header-notification__menu-list__item-header__content-about">
                    <span>{{ item.created_at_local.date | moment_from }} ago</span>
                    <span>order {{ item.orderid }}</span>
                </div>

                <div
                    v-if="item.message && item.message.length > 0"
                    class="header-notification__menu-list__item-header__content-text"
                >
                    &laquo;
                    <span>{{ item.message.substr(0, text_max_length) }}</span>
                    <span v-if="item.message.length > text_max_length">...</span>
                    &raquo;
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import filtersMixin from '@/mixins/filtersMixin'

export default {
    name: 'NewMessage',
    mixins: [
        filtersMixin
    ],
    props: {
        item: {
            required: true,
            type: Object
        },
        text_max_length: {
            required: true,
            type: String
        }
    },
    methods: {
        redirectTo(id, tab) {
            this.menuState = false;
            this.$router.push({
                name: 'details',
                params: { id },
                query: { tab }
            })
        }
    }
}
</script>
