<script>
export default {
    name: 'NewTicket',
    props: {
        item: {
            required: true,
            type: Object
        },
        text_max_length: {
            required: true,
            type: String
        }
    },
    methods: {
        redirectTo(id, tab) {
            this.menuState = false;
