} = createNamespacedHelpers('client');

export default {
    components: {
        Avatar
    },
    directives: {
        ClickOutside
    },
    mixins: [
        filtersMixin,
        overlayMixin
    ],
    data() {
        return {
            clientAccountMenu: false
        }
    },
    computed: {
        // TODO: create function to check current window width and send prop to avatar component
        ...mapState('modals', {
            logged: 'logged',
            modal_client_menu: 'modal_client_menu'
        }),
        ...mapClientFields([
            'client_data.avatar',
            'client_data.firstname',
            'client_data.lastname',
            'client_data.email',
            'clientid'
        ]),
        ...mapState('order/order_form', {
            items: 'cart_items',
            total: 'cart_total'
        }),
        ...mapState('client/client_form', {
            clientid: 'clientid',
            auth: 'auth'

        }),
        account_balance() {
            const data = this.$store.state.client.client_form.client_data
            return data.statistics ? data.statistics.account_balance : null
        },
        routeName() {
            return this.$route.name
        }
    },
    async mounted() {
        // this gets client data from the API
        // try {
        //     await this.updateClient()
        // } catch (e) {
        //     eventBus.$emit('showSnackBar', 'Failed to update client account data', 'error');
        // }
    },
    methods: {
        ...mapClientActions({
            actionLogoutClient: ACTION_LOGOUT_CLIENT,
            updateClient: ACTION_UPDATE_CLIENT_DATA
        }),
        outsideCloseMenu() {
            this.closeMenu({ overlayAvailability: true, lockAvailability: true })
        },
        async logout() {
            try {
                await this.actionLogoutClient()
                this.$router.push({ name: 'login' })
            } catch (e) {
                console.log(e)
            }
        }

    }

}
