        addNewNotification(notification) {
            // this.list.push(notification);
            this.list.unshift(notification);
            this.newMsg = true;

            setTimeout(() => {
                this.newMsg = false;
            }, 3000);
        },
        deleteNotification(id) {
            return new Promise((resolve, reject) => {
                const vm = this;
                vm.$http.post('/api/cp/notification/delete-one', {
                    id
                })
                    .then((response) => {
                        // vm.list = response.data.data
                        for (let i = 0; i < vm.list.length; i += 1) {
                            if (vm.list[i].id === id) {
                                vm.list.splice(i, 1);
                                return;
                            }
                        }
                        resolve(response)
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
        },
        deleteAllNotification() {
            return new Promise((resolve, reject) => {
                const vm = this;
                vm.$http.post('/api/cp/notification/delete-all')
                    .then((response) => {
                        vm.list = []
                        resolve(response)
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
        },
        redirectTo(id, tab) {
            this.menuState = false;
            this.$router.push({
                name: 'details',
                params: { id },
                query: { tab }
            })
        },
        redirectToTickets(orderid, ticketid) {
            this.menuState = false;
            this.$router.push({
                name: 'details',
                params: {
                    id: orderid
                },
                query: {
                    tab: 'tickets',
                    referenceid: ticketid
                }
            })
        },
        redirectToCredits() {
            this.menuState = false;
            this.$router.push({ name: 'credits' })
        },
        getScrollingPosition() {
            if (this.loading) { return }
            const scrollingBar = document.getElementById('scrollingBar');
            const listHeight = document.getElementById('listHeight');
            if (scrollingBar.scrollTop + scrollingBar.getBoundingClientRect().height >= listHeight.getBoundingClientRect().height) {
                this.loading = true;
                this.getData()
            }
        },
        outsideCloseMenu() {
            this.closeMenu({ overlayAvailability: true, lockAvailability: true })
        },
        setupNotificationByType(type) {
            switch (type) {
            case 'new_order_message':
                return 'NewMessage'
            case 'new_ticket':
                return 'NewTicket'
            case 'new_file':
                return 'NewFile'
            case 'new_ticket_reply':
                return 'NewSupportTicketReply'
            case 'new_credit':
                return 'NewCredit'
            default:
                return type;
            }
        }
    }
